<template>

    <div class="signing-documents-list-component">

        <div class="signing-documents-list">

            <div v-for="doc in documents"
                 :key="doc.id"
                 class="upload-documents-item">

                <div class="upload-documents-img">
                    <div class="upload-documents-poster">
                        {{doc.files[0].format}}
                    </div>
                </div>

                <div class="upload-documents-title">
                    <a href="#" @click.prevent="downloadFile(doc)">{{doc.name}}</a>
                    <div class="upload-documents-links">
                        <span>{{listQnt(doc.files.length)}}</span> – <a href="#" @click.prevent="downloadFile(doc)">Скачать</a>
                    </div>
                </div>

                <div class="upload-documents-status">
                    <div class="have-read-switch">
                        <div class="h6">Я ознакомился</div>
                        <FormGroupSwitchCheckbox @change="doc.haveRead = !doc.haveRead" />
                    </div>
                </div>

            </div>

        </div>

    </div>

</template>

<script>

    import FormGroupSwitchCheckbox from "@/components/Inputs/FormGroupSwitchCheckbox";

    export default {
        name: "SigningComponentsList.vue",
        props: {
            documents: Array
        },
        components: {FormGroupSwitchCheckbox},
        data: () => ({
            checkLoadedDocs: false,
            loadedDocsViews: [],
            readingArray: []
        }),
        methods: {
            listQnt(i) {
                if (i == 1) {
                    return `${i} лист`
                }
                else if (i > 1 && i < 5) {
                    return `${i} листа`
                }
                else {
                    return `${i} листов`
                }
            },
            openDocView(doc) {
                let docObject = {}
                docObject.id = doc.id
                docObject.title = doc.title
                docObject.list = []
                doc.loadedDocs.forEach(item => {
                    docObject.list.push(item)
                })
                this.loadedDocsViews = []
                this.loadedDocsViews.push(docObject)
                this.checkLoadedDocs = true
            },
            closeViewDocModal() {
                this.loadedDocsViews = []
                this.checkLoadedDocs = false
            },
            downloadFile(doc) {
                doc.files.forEach(file => {
                    this.$store.dispatch('getFile', file.id).then(blob => {
                        this.$store.dispatch('downloadFile', {content: blob, name: file.name, format: file.format})
                    })
                })
            }
        }
    }
</script>

